<template>
  <van-radio-group v-model="svalue" v-on="$listeners" v-bind="$attrs" @change="handdleChange">
    <van-radio
      v-for="item in list"
      v-on="$listeners"
      v-bind="$attrs"
      :disabled="item.disabled"
      :name="item[dictSetting.format.value]"
      :key="item[dictSetting.format.value]"
      :label="item[dictSetting.format.value]"
    >{{item[dictSetting.format.label]}}</van-radio>
  </van-radio-group>
</template>
<script>
import { state, actions } from "~/store/index.js";
export default {
  name: "van-radio-dict",
  data() {
    return {
      list: [], //
      svalue: this.value,
      type: "" //
    };
  },
  props: {
    //
    keyValue: {
      type: Boolean,
      default: false
    },
    dictType: {
      type: [String, Object]
    }, //
    value: {}, //v-model
    //
    disableObj: {
      type: Object
    },
    //
    filterDataFun: {
      type: Function
    },
    //
    disabledDataFun: {
      type: Function
    }
  },
  computed: {
    dictCodes() {
      return state.dictCodes;
    },
    dictSetting() {
      return state.dictSetting;
    },
    isGettingAllDictTypes() {
      return state.isGettingAllDictTypes;
    },
    isGettingDictTypes() {
      return state.isGettingDictTypes;
    }
  },
  watch: {
    isGettingDictTypes(val) {
      if (!val.includes(this.type)) {
        this.getDictionary_option();
      }
    },
    // 
    isGettingAllDictTypes(val) {
      if (!val) {
        this.getDictionary_option();
      }
    },
    //
    value(val) {
      this.svalue = val; //propsresultdatamyResult
    },
    svalue(val, oldVal) {
      if (val !== oldVal) {
        this.$emit("input", val); //myResult
      }
    }
  },
  mounted() {
    // this.svalue=this.value;//
    this.getDictionary_option();
  },
  methods: {
    // 
    async getDictionary_option() {
      if (!this.dictType) {
        return;
      }
      //
      let dictData = [];
      if (typeof this.dictType === "object") {
        this.type = this.dictType.type;
        await actions.getDictDataObj(this.type);
        dictData = [].concat(this.dictCodes[this.type]);
        const {
          filters,
          filterType = this.dictSetting.format.value,
          reverse = false
        } = this.dictType;
        const filtersArr =
          typeof filters === "string" ? filters.split(",") : filters;
        if (reverse) {
          dictData = dictData.filter(item => {
            return !filtersArr.includes(item[filterType]);
          });
        } else {
          dictData = dictData.filter(item => {
            return filtersArr.includes(item[filterType]);
          });
        }
      } else {
        this.type = this.dictType;
        await actions.getDictDataObj(this.type);
        dictData = [].concat(this.dictCodes[this.type]);
      }

      //
      if (this.filterDataFun) {
        dictData = this.filterDataFun(dictData);
      } else {
        dictData = this.dictSetting.filterDataFun(dictData);
      }
      //
      if (this.disabledDataFun) {
        dictData = this.disabledDataFun(dictData);
      } else {
        dictData = this.dictSetting.disabledDataFun(dictData);
      }

      if (this.disableObj) {
        const {
          disableValue,
          disableType = this.dictSetting.format.value,
          reverse = false
        } = this.disableObj;
        const disableList =
          typeof disableValue === "string"
            ? disableValue.split(",")
            : disableValue;
        if (reverse) {
          dictData = dictData.map(item => {
            const targetItem = JSON.parse(JSON.stringify(item));
            targetItem.disabled =
              !disableList.includes(targetItem[disableType]) ||
              targetItem.disabled;
            return targetItem;
          });
        } else {
          dictData = dictData.map(item => {
            const targetItem = JSON.parse(JSON.stringify(item));
            targetItem.disabled =
              disableList.includes(targetItem[disableType]) ||
              targetItem.disabled;
            return targetItem;
          });
        }
      }
      this.list = dictData;
    },
    // 
    handdleChange() {
      /* emit
         */
      if (!this.keyValue) {
        this.$emit("dictChange", this.svalue);
        return;
      }
      this.$emit(
        "dictChange",
        this.list.find(item => {
          return item[this.dictSetting.format.value] === this.svalue;
        })
      );
    }
  }
};
</script>