require("~/assets/css/index.css") 
require("./vant-syx/cascader/style/index")

//index.jsrequire.context()
/*
    require.context(arg1,arg2,arg3)
        arg1 - 
        arg2 - 
        arg3 - 
    Api
*/
const { mutations} = require("~/store/index.js") 

//element
// const {} = require("element-ui") 
// 
const exportObj = {}

const packageObj = require("../package.json")
//
var version = packageObj.version
exportObj.version = version

// 
const components = []
const componentList = require.context("./components", true, /\.js$/)
componentList.keys().forEach(fileName => {
  const file = componentList(fileName)
  exportObj[file.default.name] = file.default || file
  components.push(file.default || file)
})

//
const functions = []
const fnList = require.context("./functions", true, /\.js$/)
fnList.keys().forEach(fileName => {
  const file = fnList(fileName)
  for (const key in file) {
    //
    exportObj[key] = file[key]
    var fnObj = {
      name: "$" + key,
      fn: file[key]
    }
    functions.push(fnObj)
  }
})

//
const filters = []
const filterList = require.context("./filters", true, /\.js$/)
filterList.keys().forEach(fileName => {
  const file = filterList(fileName)
  const whiteNameList = ["getLabelByCode", "getLabelByCodes", "getCodeByLabel", "getCodeByLabels", "getTreeLabelByCode", "getTreeCodeByLabel", "getTreeLabelByCodes", "getTreeCodeByLabels"]
  for (const key in file) {
    if (!whiteNameList.includes(key)) {
      //
      exportObj[key] = file[key]
    }
    var filterObj = {
      name: key,
      fn: file[key]
    }
    filters.push(filterObj)
  }
})

//
const directives = []
const directiveList = require.context("./directives", true, /\.js$/)
directiveList.keys().forEach(fileName => {
  const file = directiveList(fileName)
  for (const key in file) {
    //
    exportObj[key] = file[key]
    var directiveObj = {
      name: key,
      fn: file[key]
    }
    directives.push(directiveObj)
  }
})

//
async function getDIctCodeReq(setting) {
  // 
  const dictData = setting.storage.getItem(setting.dictDataKey)
  //usuallyGetFileds       
  const values = setting.usuallyGetFileds.split(",")
  const needGetArr = [] //
  if (values.length > 0) {
    for (let i = 0; i < values.length; i++) {
      if (dictData[values[i]]) {
        continue
      }
      needGetArr.push(values[i])
    }
  }
 
  if (!dictData || setting.getDictEveryTime || needGetArr.length > 0) {
    let data
    let query
    if (setting.isGetAll) {
      query = ""
      mutations.SETTING_ISGETTING_ALL_DICT_TYPES(true) // true   
      data = await setting.getDictCodeApi(setting.formatterRequest(setting.query))
      mutations.SETTING_ISGETTING_ALL_DICT_TYPES(false) //  false   
    } else if (needGetArr.length > 0) {
      query = needGetArr.join(",")
      mutations.ADD_IS_EGTTING_DICT_TYPES(needGetArr.join(",")) //
      data = await setting.getDictCodeApi(setting.formatterRequest(setting.query, query))
      mutations.REMOVE_IS_EGTTING_DICT_TYPES(needGetArr.join(",")) //
    }
    //
    const dictCodeList = setting.formatterDictList(data, query)
    const verssion = setting.formatterDictVerssion(data)
    setting.storage.setItem(setting.verssionKey, verssion) //

    //
    const dictObjs = Object.assign(JSON.parse(JSON.stringify(setting.localDictCodes)), dictCodeList)
    mutations.SET_DICT_CODES(dictObjs)
    setting.storage.setItem(setting.dictDataKey, JSON.stringify(dictObjs)) //
    
  }
}

//  install  Vue  use 
const install = async function(Vue, config) {
  // 
  if (install.installed) { return }
  //van
  const {Cascader} = require("~/vant-syx")
  const {Popup, Picker, Field, RadioGroup, Radio, Checkbox, CheckboxGroup, Toast, DatetimePicker, Calendar} = config.vant ? config.vant : require("vant")
  Vue.use(Popup).use(Picker).use(Field).use(RadioGroup).use(Radio).use(Checkbox).use(CheckboxGroup).use(Cascader).use(DatetimePicker).use(Calendar)
  Vue.prototype.$toast = Toast

  /**
 * @description: 
 * @param {*} Vue
 * @param {*} config :{
 *    axiosSetting,    axios   requestName
 *    getDictCodeApi,    
 *    verssion,     
 *    query,    type
 *    usuallyGetFileds,      
 *    verssionKey, 
 *    dictDataKey, 
 *    format, 
 *    formatterDictList,  
 *    formatterDictVerssion, 
 *    formatterRequest, 
 *    storage,    localstorage   sessionstorage
 *    isGetAll,  
 *    treeSetting, 
 *    localDictCodes, 
 *    getDictEveryTime  
 *    filterDataFun  
 *    disabledDataFun  
 * }
 * @return {*}
 * @author: syx
 */
  const defaultConfig = {
    // 
    filterDataFun: (list) => {
      return list
    },
    // 
    disabledDataFun: (list) => {
      return list
    },
    //
    treeSetting: {
      idField: "id", //
      parentIdField: "parentId", //
      childrenField: "children", //
      firstId: "0", // 
      labelField: "label", //label
      labelArrField: "labelArr", //
      idArrField: "idArr", //id
      levelField: "level", //
      level: 1, // 
      leafField: "leaf" //
    },
    query: "type",
    verssionKey: "currentVerssion",
    dictDataKey: "dictCodeList",
    format: {
      value: "value",
      label: "label"
    },
    formatterRequest: (query, data) => {
      if (!data) {
        return {[query]: ""}
      }
      return {[query]: data} 
    },
    formatterDictList: (data, query) => {
      return data.data.dictCodeList
    },
    formatterDictVerssion: (data) => {
      return data.data.verssion
    },
    storage: localStorage,
    usuallyGetFileds: "",
    isGetAll: false,
    localDictCodes: {}, //
    getDictEveryTime: false
  }
  config.treeSetting = Object.assign(defaultConfig.treeSetting, config.treeSetting)
  const setting = Object.assign(defaultConfig, config)

  mutations.SET_DICT_SETTING(setting)
  const dictList = JSON.parse(setting.storage.getItem(setting.dictDataKey)) || {}
  //
  let dictObjs = JSON.parse(JSON.stringify(setting.localDictCodes))
  dictObjs = Object.assign(dictList, dictObjs)
  setting.storage.setItem(setting.dictDataKey, JSON.stringify(dictObjs))
  mutations.SET_DICT_CODES(dictObjs)

  //
  const currentVerssion = setting.storage.getItem(setting.verssionKey) //
  if (setting.verssion !== currentVerssion) {
    setting.storage.removeItem(setting.dictDataKey) //
    setting.storage.setItem(setting.verssionKey, setting.verssion) //

    setting.storage.setItem(setting.dictDataKey, JSON.stringify(setting.localDictCodes))
    // 
    mutations.SET_DICT_CODES(setting.localDictCodes)
  }
  if (setting.isGetAll || setting.usuallyGetFileds) {
    //   
    getDIctCodeReq(setting)
  }

  // 
  components.map(component => Vue.component(component.name, component))

  //
  functions.map(fn => {
    Vue.prototype[fn.name] = fn.fn
  })

  //
  filters.map(filter => {
    const whiteNameList = ["getLabelByCode", "getLabelByCodes", "getCodeByLabel", "getCodeByLabels", "getTreeLabelByCode", "getTreeCodeByLabel", "getTreeLabelByCodes", "getTreeCodeByLabels"]
    if (!whiteNameList.includes(filter.name)) {
      Vue.prototype[`$${filter.name}`] = filter.fn
    }
    Vue.filter(filter.name, filter.fn)
  })

  //
  directives.map(directive => {
    if (typeof (directive.fn) === "object") {
      Vue.directive(directive.name, directive.fn)
    } else if (typeof (directive.fn) === "function") {
      Vue.directive(directive.name, {
        inserted: directive.fn
      })
    }
  })
}

// Vue
if (typeof window !== "undefined" && window.Vue) {
  install(window.Vue)
}

// 
module.exports = exportObj

//
module.exports.install = install
