/**
 *17350800661 => 173****0661
 *
 * @export
 * @param {*} tel 
 * @returns
 */
export function mobile(str) {
  return rule(str, 3, 4)
}

export function address(str, placeholder = 4) {
  return rule(str, 3, 4, placeholder)
}
/**
 *123456789012345678 => 123******5678
 *
 * @export
 * @param {*} idcard 
 * @returns
 */
export function idCard(str) {
  return rule(str, 3, 4)
}

/**
 * 
 * @param {string} val 
 * @param {number} front 
 * @param {number} back 
 * @param {number} placeholder 
 */
export function rule(val, front, back, placeholder) {
  if (!val){
    return ""
  }
  const length = val.length
  placeholder = placeholder || length - front - back

  if (length > front + back) {
    const frontVal = val.slice(0, front)
    const backVal = back ? val.slice(-back) : ""
    return frontVal + "*".repeat(placeholder) + backVal
  }

  return val
}

const Type = {
  mobile,
  name,
  address,
  idCard
}

/**
 * 
 * @param {string} val 
 * @param {string|number} type typeType
 * @param {string} defaultVal 
 * @param {number} back 
 * @param {number} placeholder 
 */
const Desensitization = (val, type, defaultVal, back, placeholder) => {
  if (!val) {
    return defaultVal
  }
  if (Type[type]) {
    return Type[type](val)
  } else {
    // 
    const front = type
    return rule(val, front, back, placeholder)
  }
}

Desensitization.install = function(Vue){
  Vue.filter("desensitization", Desensitization)
}
export const desensitization = Desensitization

/**
 *10000 => "10,000"
 *
 * @export
 * @param {*} num 
 * @returns
 */
export function toThousandFilter(num) {
  return (+num || 0).toString().replace(/^-?\d+/g, m => m.replace(/(?=(?!\b)(\d{3})+$)/g, ","))
}

/**
 * => *
 * => *
 * => *
 * @export
 * @param {*} name 
 * @returns
 */
const doubleSurnname = [
  "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "",
  "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", ""
]
export function name(str) {
  const fTwo = String(str).substr(0, 2),
    fOne = String(str).substr(0, 1),
    eOne = String(str).substr(str.length - 1),
    ifDoubleSur = doubleSurnname.toString().indexOf(fTwo) > -1
  if (!str) {
    return false
  } else if (String(str).length === 2) {
    return fOne + "*"
  } else if (String(str).length === 3) {
    if (ifDoubleSur) { //3
      return fTwo + "*"
    } else {
      return fOne + "*" + eOne
    }
  } else if (String(str).length === 4) { //4
    if (ifDoubleSur) { //
      return fTwo + "*" + eOne
    } else {
      return fOne + "*" + "*" + eOne
    }
  } else { //4
    return fOne + "*" + "*" + eOne
  }
}

/**
 *
 * @export
 * @param {*} str 34
 * @returns
 */
export function strf3e4(str) {
  if (!str) {
    return ""
  } else if (str.length > 7) {
    return String(str).substr(0, 3) + "***********" + String(str).substr(str.length - 4)
  } else {
    return str
  }
}
