/**
 * @description:   
 * @param {*} arr 
 * @param {*} parentIdArr id
 * @param {*} levelField  
 * @param {*} level       
 * @return {*}
 * @author: syx
 */
function order(arr, parentIdArr, config) {
  let defaultConfig = {
    levelField: "level",
    level: 1,
    idField: "id",
    parentIdField: "parentId"
  }
  defaultConfig = Object.assign(defaultConfig, config)
  if (arr.length === 0) { return [] }
  const firstLevelArr = arr.filter(item => parentIdArr.includes(item[defaultConfig.parentIdField])).map(item => {
    return {
      ...item,
      [defaultConfig.levelField]: defaultConfig.level
    }
  })
  const restArr = arr.filter(item => !parentIdArr.includes(item[defaultConfig.parentIdField]))
  const nextParentIdArr = firstLevelArr.map(item => item[defaultConfig.idField])
  defaultConfig.level++
  return firstLevelArr.concat(order(restArr, nextParentIdArr, defaultConfig))
}

/**
 * @description: code
 * @param {*} id
 * @param {*} arr
 * @param {*} options
 * @return {*}
 * @author: syx
 */
export function getTreeItemByCode(id, arr, options) {
  const defaultOptions = {
    idField: "id", //
    parentIdField: "parentId" //
  }
  Object.assign(defaultOptions, options)
  const item = arr.find(item => item[defaultOptions.idField] === id)
  if (!item) {
    return []
  }
  return getTreeItemByCode(item[defaultOptions.parentIdField], arr, defaultOptions).concat([item])
}

/**
 * @description: label
 * @param {*} id
 * @param {*} arr
 * @param {*} options
 * @return {*}
 * @author: syx
 */
export function getTreeItemByLabel(label, arr, options) {
  const defaultOptions = {
    idField: "id",
    labelField: "label", //
    parentIdField: "parentId" //
  }
  Object.assign(defaultOptions, options)
  const item = arr.find(item => item[defaultOptions.labelField] === label)
  return getTreeItemByCode(item[defaultOptions.idField], arr, defaultOptions)
}