<template>
  <div class="van-calendar-dict">
    <van-field v-on="$listeners" type="text" v-bind="$attrs" readonly clickable :value="showValue" is-link  @click="handleClick" />
    <van-calendar 
      ref="calendar"
      v-bind="$attrs"
      v-model="showPicker"
      @select="select"
      @open="open"
      @close="close"
      @opened="opened"
      @closed="closed"
      @unselect="unselect"
      @month-show="monthShow"
      @over-range="overRange"
      @click-subtitle="clickSubtitle"
      @confirm="onConfirm" />
  </div>
</template>
<script>
export default {
  name: "van-calendar-dict",
  data() {
    return {
      showValue: "",
      showPicker: false,
      svalue: this.value
    }
  },
  props: {
    formateShowValue: {
      type: Function,
      default: (dates) => {
        if (dates&&dates.length > 0) {
          return ` ${dates.length} `
        }
        return ""
      }
    },
    //
    dateSpacer: {
      type: String,
      default: "  "
    },
    //
    spacer: {
      type: String,
      default: "-"
    },
    //
    defaultValue: {
      type: String,
      default: ""
    },
    value: {
    } //v-model

  },
  computed: {

  },
  watch: {
    //
    value(val) {
      this.svalue = val//propsresultdatamyResult
    },
    svalue(val, oldVal) {
      if (val !== oldVal) {
        this.$emit("input", val)//myResult
      }
    },
    showPicker(val) {
      if (val) {
        this.$nextTick(() => {
          if (this.$attrs.type === "multiple") {
            if (this.value&&this.value.length > 0) {
              const dateArr = []
              for (let i = 0; i < this.value.length; i++) {
                dateArr.push(new Date(this.value[i].replace(/-|\./g, "/")))
              }
              this.$refs.calendar.currentDate = dateArr
            } else {
              this.$refs.calendar.currentDate = []
            }
          } else if (this.$attrs.type === "range") {
            if (this.value&&this.value.length === 2) {
              this.$refs.calendar.currentDate = [
                new Date(this.value[0].replace(/-|\./g, "/")),
                new Date(this.value[1].replace(/-|\./g, "/"))
              ]
            } else {
              this.$refs.calendar.currentDate = new Date()
            }
          } else {
            this.$refs.calendar.currentDate = new Date(this.value.replace(/-|\./g, "/"))
          }
        })
      }
    }
  },
  created() {
    if (this.$attrs.type === "multiple") {
      this.showValue = this.formateShowValue(this.value)
      this.$nextTick(() => {
        if (this.value&&this.value.length > 0) {
          const dateArr = []
          for (let i = 0; i < this.value.length; i++) {
            dateArr.push(new Date(this.value[i].replace(/-|\./g, "/")))
          }
          this.$refs.calendar.currentDate = dateArr
        } else {
          this.$refs.calendar.currentDate = []
        }
      })

    } else if (this.$attrs.type === "range") {
      this.showValue = (this.value&&this.value.length === 2) ? this.value[0].replace(/-|\./g, this.spacer) + this.dateSpacer + this.value[1].replace(/-|\./g, this.spacer) : ""
      this.$nextTick(() => {
        if (this.value.length === 2) {
          this.$refs.calendar.currentDate = [
            new Date(this.value[0].replace(/-|\./g, "/")),
            new Date(this.value[1].replace(/-|\./g, "/"))
          ]
        }
      })
    } else {
      this.showValue = this.$dayFormat(this.value, `YYYY${this.spacer}MM${this.spacer}DD`, this.defaultValue)
      this.$nextTick(() => {
        if (this.showValue !== this.defaultValue) {
          this.$refs.calendar.currentDate = new Date(this.value.replace(/-|\./g, "/"))
        }
      })
    }
  },
  methods: {
    clickSubtitle(value) {
      this.$emit("click-subtitle", value)
    },
    overRange(value) {
      this.$emit("over-range", value)
    },
    monthShow(value) {
      this.$emit("month-show", value)
    },
    unselect(value) {
      this.$emit("unselect", value)
    },
    closed() {
      this.$emit("closed")
    },
    opened() {
      this.$emit("opened")
    },
    close() {
      this.$emit("close")
    },
    open() {
      this.$emit("open")
    },
    select(value) {
      this.$emit("select", value)
    },
    //
    handleChange(value) {
      this.$emit("change", value)
    },
    //
    handleCancel() {
      this.$emit("cancel")
    },
    //
    handleClick() {
      if (this.$attrs.disabled === true || this.$attrs.disabled === "") {
        return 
      }
      this.showPicker = true
    },
    //
    onConfirm(value) {
      if (this.$attrs.type === "multiple") {
        if (value&&value.length > 0) {
          const dateArr = []
          for (let i = 0; i < value.length; i++) {
            dateArr.push(this.$dayFormat(value[i], `YYYY${this.spacer}MM${this.spacer}DD`, this.defaultValue))
          }
          this.svalue = dateArr
        } else {
          this.svalue = []
        }
        this.showValue = this.formateShowValue(this.svalue)
      } else if (this.$attrs.type === "range") {
        if (value&&value.length === 2) {
          this.svalue = [
            this.$dayFormat(value[0], `YYYY${this.spacer}MM${this.spacer}DD`, this.defaultValue),
            this.$dayFormat(value[1], `YYYY${this.spacer}MM${this.spacer}DD`, this.defaultValue)
          ]
          this.showValue = this.svalue[0].replace(/-|\./g, this.spacer) + this.dateSpacer + this.svalue[1].replace(/-|\./g, this.spacer)
        } else {
          this.showValue = ""
        }
      } else {
        this.svalue = this.$dayFormat(value, `YYYY${this.spacer}MM${this.spacer}DD`, this.defaultValue)
        this.showValue = this.svalue
      }
      this.showPicker = false
      this.$emit("confirm", value)
    }
  }
}
</script>
<style lang="less" scoped>
.van-datetime-dict{
  ::v-deep .slot-btn{
    .select{
      margin-right: 14px;
    }
  }
}

</style>