import Vue from "vue"

export const state = Vue.observable({ 
  dictSetting: {},
  dictCodes: {},
  isGettingDictTypes: [], // 
  isGettingAllDictTypes: false, //
  unfindDictTypes: [] //
})

export const mutations = {
  //
  SET_DICT_SETTING(setting) {
    state.dictSetting = setting
  },
  //
  SET_DICT_CODES(dictData) {
    state.dictCodes = dictData
  },
  //
  ADD_IS_EGTTING_DICT_TYPES(typesStr) {
    console.log(typesStr)
    const dictArr = typesStr.split(",")
    const isGettingArr = [...dictArr, ...state.isGettingDictTypes]
    const targetArr = new Set(isGettingArr)
    state.isGettingDictTypes = Array.from(targetArr)
  },
  //
  REMOVE_IS_EGTTING_DICT_TYPES(typesStr) {
    const dictArr = typesStr.split(",")
    //arr1 arr2
    const targetArr = [...new Set(state.isGettingDictTypes)].filter(item => !new Set(dictArr).has(item))
    state.isGettingDictTypes = Array.from(targetArr)
  },
  //
  SETTING_ISGETTING_ALL_DICT_TYPES(status) {
    state.isGettingAllDictTypes = status
  },

  //
  ADD_UNFIND_DICT_TYPES(typesStr) {
    const dictArr = typesStr.split(",")
    const isGettingArr = [...dictArr, ...state.unfindDictTypes]
    const targetArr = new Set(isGettingArr)
    state.unfindDictTypes = Array.from(targetArr)
  },
  //
  REMOVE_UNFIND_DICT_TYPES(typesStr) {
    const dictArr = typesStr.split(",")
    //arr1 arr2
    const targetArr = [...new Set(state.unfindDictTypes)].filter(item => !new Set(dictArr).has(item))
    state.unfindDictTypes = Array.from(targetArr)
  }
}

export const actions = {
  getDictDataObj(dictType) {
    return new Promise((resolve, reject) => {
      const { storage, dictDataKey, getDictCodeApi, usuallyGetFileds, formatterRequest, query, verssionKey, localDictCodes, formatterDictList, formatterDictVerssion, isGetAll } = state.dictSetting
      //
      const dictCodeList = state.dictCodes || localDictCodes
      //
      if (!dictCodeList[dictType] || dictCodeList[dictType].length === 0) {
        if (state.isGettingDictTypes.includes(dictType) || state.isGettingAllDictTypes) { return } //  
        mutations.ADD_IS_EGTTING_DICT_TYPES(dictType) //
        if (state.unfindDictTypes.includes(dictType)) {
          // console.log(`${dictType} `)
          reject(`${dictType} `)
          return
        }
        // 
        mutations.ADD_UNFIND_DICT_TYPES(dictType)

        getDictCodeApi(formatterRequest(query, dictType)).then(data => {
          mutations.REMOVE_IS_EGTTING_DICT_TYPES(dictType) //

          const verssion = formatterDictVerssion(data)
          const currentVerssion = storage.getItem(verssionKey) //
          if (verssion !== currentVerssion) { //
            let requestField = ""
            if (isGetAll) { //
              requestField = ""
              mutations.SETTING_ISGETTING_ALL_DICT_TYPES(true) // true   
            } else {
              if (usuallyGetFileds) { //
                const storageTypeArr = Object.keys(dictCodeList) //
                const usuallyGetTypeArr = usuallyGetFileds.split(",")
                const arr = storageTypeArr.concat(usuallyGetTypeArr)
                arr.push(dictType)
                const resultArr = Array.from(new Set(arr))
                requestField = resultArr.join(",")
              } else {
                const storageTypeArr = Object.keys(dictCodeList) //
                storageTypeArr.push(dictType)
                const resultArr = Array.from(new Set(storageTypeArr))
                requestField = resultArr.join(",")
              }
              mutations.ADD_IS_EGTTING_DICT_TYPES(requestField) //
            } 
            
            //
            getDictCodeApi(formatterRequest(query, requestField)).then(data => {
              const allDictData = formatterDictList(data, dictType)
              if (!(allDictData[dictType]&&allDictData[dictType].length > 0)) {
                // console.log(`${dictType} `)
                reject(`${dictType} `)
                return
              }
              //   
              mutations.REMOVE_UNFIND_DICT_TYPES(dictType)
              const verssion = formatterDictVerssion(data)
              const dictObjs = Object.assign(JSON.parse(JSON.stringify(localDictCodes)), allDictData)
              storage.setItem(dictDataKey, JSON.stringify(dictObjs))
              storage.setItem(verssionKey, verssion)
              mutations.SET_DICT_CODES(dictObjs)
              resolve(state.dictCodes)
              if (state.isGettingAllDictTypes) {
                mutations.SETTING_ISGETTING_ALL_DICT_TYPES(false) //  false   
              } else {
                mutations.REMOVE_IS_EGTTING_DICT_TYPES(requestField) //
              }
            })
          } else {
            const typeDictList = formatterDictList(data, dictType)
            if (!(typeDictList[dictType]&&typeDictList[dictType].length > 0)) {
              // console.log(`${dictType} `)
              reject(`${dictType} `)
              return
            }
            
            //   
            mutations.REMOVE_UNFIND_DICT_TYPES(dictType)
            //
            const dictData = state.dictCodes || localDictCodes
            Object.assign(dictData, typeDictList) //
            storage.setItem(dictDataKey, JSON.stringify(dictData)) //
            mutations.SET_DICT_CODES(dictData)
            resolve(state.dictCodes)
          }
        })
      } else {
        //   
        mutations.REMOVE_UNFIND_DICT_TYPES(dictType)

        mutations.SET_DICT_CODES(dictCodeList)
        resolve(state.dictCodes)
      }
    })
  }
}